.contact{
  padding: 6%;
}
.contact__grid{
  display: grid;
  min-width: 32rem;
  max-width: 80rem;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1rem;
  padding: 1rem;
}
.item__contact{
  overflow: hidden;
}
.item__contact img{
  width: 90px;
  padding: 5px;
  padding-left: 0px;
  text-align: center;
}
.item- img{
  max-width: 100px;
  padding-left: 5px;
}
.item-contact-ig img{
  max-width: 100px;
}